import './App.scss';
import $ from 'jquery';
import React, { Suspense } from 'react';
import { useEffect } from 'react';
import { Route, Switch } from 'react-router-dom'
import { datosFirebaseUsuario } from './components/utils/Firebase';

// Importar vistas
const Links = React.lazy(() => import('./components/Links'))

function App() {
  useEffect(() => {
    datosFirebaseUsuario()
    .then((data) => {
        let background = data.background ? data.background : './img/fondo.jpg';
        $('body').css({
          background: 'linear-gradient(180deg, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, 0.2) 20%, rgba(0, 0, 0, 1) 100%), url('
          + background +')',
          'background-repeat': 'no-repeat',
          'background-size': 'cover',
          'background-color': '#000000' 
        });
    });    
  });

  return (
    <div className="container">
      <Suspense fallback={<div></div>}>
        <Switch>
          <Route exact path="/" render={() => <Links />} />
        </Switch>
      </Suspense>
    </div>
  );
}

export default App;
